'use client';

import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import { StarIcon, MagnifyingGlassIcon, AdjustmentsHorizontalIcon } from '@heroicons/react/24/outline';

// بيانات وهمية لمزودي الخدمات
const serviceProviders = [
  {
    id: 1,
    name: 'أحمد محمد',
    avatar: '/images/providers/provider1.jpg',
    rating: 4.9,
    reviewCount: 128,
    completedJobs: 145,
    specialties: ['توليد الصور', 'تحسين الصور', 'تصميم الشخصيات'],
    bio: 'متخصص في توليد وتحسين الصور باستخدام تقنيات الذكاء الاصطناعي المتقدمة. خبرة 5 سنوات في مجال الفن الرقمي والتصميم.',
    hourlyRate: 35,
    isOnline: true,
    responseTime: 'خلال ساعة',
  },
  {
    id: 2,
    name: 'سارة أحمد',
    avatar: '/images/providers/provider2.jpg',
    rating: 4.7,
    reviewCount: 93,
    completedJobs: 112,
    specialties: ['النماذج اللغوية الكبيرة', 'معالجة اللغة العربية', 'تطوير شات بوت'],
    bio: 'مطورة متخصصة في النماذج اللغوية الكبيرة ومعالجة اللغة العربية. أعمل على تطوير حلول ذكاء اصطناعي مخصصة للشركات.',
    hourlyRate: 45,
    isOnline: false,
    responseTime: 'خلال 3 ساعات',
  },
  {
    id: 3,
    name: 'محمود علي',
    avatar: '/images/providers/provider3.jpg',
    rating: 5.0,
    reviewCount: 76,
    completedJobs: 89,
    specialties: ['توليد الصوت', 'تحويل النص إلى كلام', 'تحسين الصوت'],
    bio: 'خبير في تقنيات توليد الصوت وتحويل النص إلى كلام باستخدام الذكاء الاصطناعي. متخصص في اللهجات العربية المختلفة.',
    hourlyRate: 40,
    isOnline: true,
    responseTime: 'خلال ساعتين',
  },
  {
    id: 4,
    name: 'ليلى خالد',
    avatar: '/images/providers/provider4.jpg',
    rating: 4.6,
    reviewCount: 54,
    completedJobs: 67,
    specialties: ['توليد الفيديو', 'تحرير الفيديو', 'الانيميشن'],
    bio: 'متخصصة في توليد وتحرير الفيديو باستخدام أحدث تقنيات الذكاء الاصطناعي. خبرة في تصميم الانيميشن والمؤثرات البصرية.',
    hourlyRate: 50,
    isOnline: false,
    responseTime: 'خلال يوم',
  },
  {
    id: 5,
    name: 'عمر حسن',
    avatar: '/images/providers/provider5.jpg',
    rating: 4.8,
    reviewCount: 112,
    completedJobs: 134,
    specialties: ['برمجة الذكاء الاصطناعي', 'تطوير تطبيقات', 'معالجة البيانات'],
    bio: 'مهندس برمجيات متخصص في تطوير تطبيقات الذكاء الاصطناعي وحلول معالجة البيانات. خبرة في التعلم الآلي وعلوم البيانات.',
    hourlyRate: 55,
    isOnline: true,
    responseTime: 'خلال ساعة',
  },
  {
    id: 6,
    name: 'نورا سامي',
    avatar: '/images/providers/provider6.jpg',
    rating: 4.5,
    reviewCount: 48,
    completedJobs: 52,
    specialties: ['تصميم واجهات المستخدم', 'تجربة المستخدم', 'تصميم المواقع'],
    bio: 'مصممة واجهات مستخدم متخصصة في دمج تقنيات الذكاء الاصطناعي في تصميم المواقع والتطبيقات لتحسين تجربة المستخدم.',
    hourlyRate: 38,
    isOnline: false,
    responseTime: 'خلال 4 ساعات',
  },
];

// فئات التخصصات
const specialtyCategories = [
  'توليد الصور',
  'تحسين الصور',
  'النماذج اللغوية الكبيرة',
  'معالجة اللغة العربية',
  'توليد الصوت',
  'توليد الفيديو',
  'برمجة الذكاء الاصطناعي',
  'تصميم واجهات المستخدم',
  'تطوير شات بوت',
  'معالجة البيانات',
];

interface Provider {
  id: number;
  name: string;
  avatar: string;
  rating: number;
  reviewCount: number;
  completedJobs: number;
  specialties: string[];
  bio: string;
  hourlyRate: number;
  isOnline: boolean;
  responseTime: string;
}

export default function ProvidersPage() {
  const [providers, setProviders] = useState<Provider[]>(serviceProviders);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedSpecialties, setSelectedSpecialties] = useState<string[]>([]);
  const [priceRange, setPriceRange] = useState<[number, number]>([0, 100]);
  const [onlineOnly, setOnlineOnly] = useState(false);
  const [sortBy, setSortBy] = useState('rating');
  const [isFiltersOpen, setIsFiltersOpen] = useState(false);
  
  // تصفية وترتيب مزودي الخدمات
  const filteredProviders = serviceProviders
    .filter((provider) => {
      // تصفية حسب البحث
      const matchesSearch = searchTerm === '' || 
        provider.name.includes(searchTerm) || 
        provider.specialties.some(specialty => specialty.includes(searchTerm)) ||
        provider.bio.includes(searchTerm);
      
      // تصفية حسب التخصصات
      const matchesSpecialties = selectedSpecialties.length === 0 || 
        selectedSpecialties.some(specialty => provider.specialties.includes(specialty));
      
      // تصفية حسب نطاق السعر
      const matchesPrice = provider.hourlyRate >= priceRange[0] && provider.hourlyRate <= priceRange[1];
      
      // تصفية حسب الحالة (متصل)
      const matchesOnline = !onlineOnly || provider.isOnline;
      
      return matchesSearch && matchesSpecialties && matchesPrice && matchesOnline;
    })
    .sort((a, b) => {
      // ترتيب النتائج
      switch (sortBy) {
        case 'rating':
          return b.rating - a.rating;
        case 'price_low':
          return a.hourlyRate - b.hourlyRate;
        case 'price_high':
          return b.hourlyRate - a.hourlyRate;
        case 'completedJobs':
          return b.completedJobs - a.completedJobs;
        default:
          return 0;
      }
    });
  
  // إضافة أو إزالة تخصص من القائمة المحددة
  const toggleSpecialty = (specialty: string) => {
    if (selectedSpecialties.includes(specialty)) {
      setSelectedSpecialties(selectedSpecialties.filter(s => s !== specialty));
    } else {
      setSelectedSpecialties([...selectedSpecialties, specialty]);
    }
  };
  
  return (
    <div className="min-h-screen flex flex-col">
      <Navbar />
      
      <main className="flex-grow bg-gray-50">
        <div className="max-w-7xl mx-auto py-8 px-4 sm:px-6 lg:px-8">
          <div className="mb-8 text-center">
            <h1 className="text-3xl font-bold text-gray-900">خبراء الذكاء الاصطناعي</h1>
            <p className="mt-2 text-sm text-gray-600">
              ابحث عن مزودي خدمات الذكاء الاصطناعي المتخصصين في مختلف المجالات
            </p>
          </div>
          
          {/* شريط البحث */}
          <div className="w-full max-w-3xl mx-auto mb-8">
            <div className="relative flex items-center">
              <input
                type="text"
                placeholder="ابحث عن اسم أو تخصص..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-indigo-500 focus:border-indigo-500 pr-10"
              />
              <MagnifyingGlassIcon className="absolute left-3 w-5 h-5 text-gray-400" />
              <button
                onClick={() => setIsFiltersOpen(!isFiltersOpen)}
                className="mr-2 flex items-center px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 bg-white hover:bg-gray-50"
              >
                <AdjustmentsHorizontalIcon className="h-5 w-5 ml-1" />
                فلترة
              </button>
            </div>
          </div>
          
          <div className="lg:grid lg:grid-cols-4 lg:gap-x-6">
            {/* قسم الفلاتر */}
            <div className={`lg:col-span-1 ${isFiltersOpen ? 'block' : 'hidden lg:block'}`}>
              <div className="bg-white p-4 rounded-lg shadow-sm">
                <div className="mb-6">
                  <h3 className="text-lg font-medium text-gray-900 mb-3">التخصصات</h3>
                  <div className="space-y-2">
                    {specialtyCategories.map((specialty) => (
                      <div key={specialty} className="flex items-center">
                        <input
                          id={`specialty-${specialty}`}
                          type="checkbox"
                          checked={selectedSpecialties.includes(specialty)}
                          onChange={() => toggleSpecialty(specialty)}
                          className="h-4 w-4 text-indigo-600 border-gray-300 rounded"
                        />
                        <label htmlFor={`specialty-${specialty}`} className="mr-2 text-sm text-gray-700">
                          {specialty}
                        </label>
                      </div>
                    ))}
                  </div>
                </div>
                
                <div className="mb-6">
                  <h3 className="text-lg font-medium text-gray-900 mb-3">نطاق السعر</h3>
                  <div className="space-y-2">
                    <div className="flex justify-between">
                      <span className="text-sm text-gray-600">${priceRange[0]}</span>
                      <span className="text-sm text-gray-600">${priceRange[1]}</span>
                    </div>
                    <input
                      type="range"
                      min="0"
                      max="100"
                      value={priceRange[1]}
                      onChange={(e) => setPriceRange([priceRange[0], parseInt(e.target.value)])}
                      className="w-full"
                    />
                  </div>
                </div>
                
                <div className="mb-6">
                  <div className="flex items-center">
                    <input
                      id="online-only"
                      type="checkbox"
                      checked={onlineOnly}
                      onChange={() => setOnlineOnly(!onlineOnly)}
                      className="h-4 w-4 text-indigo-600 border-gray-300 rounded"
                    />
                    <label htmlFor="online-only" className="mr-2 text-sm text-gray-700">
                      متصل حاليًا فقط
                    </label>
                  </div>
                </div>
                
                <div>
                  <h3 className="text-lg font-medium text-gray-900 mb-3">ترتيب حسب</h3>
                  <select
                    value={sortBy}
                    onChange={(e) => setSortBy(e.target.value)}
                    className="w-full border border-gray-300 rounded-md p-2 text-sm"
                  >
                    <option value="rating">التقييم (الأعلى أولًا)</option>
                    <option value="price_low">السعر (الأقل أولًا)</option>
                    <option value="price_high">السعر (الأعلى أولًا)</option>
                    <option value="completedJobs">المشاريع المكتملة</option>
                  </select>
                </div>
              </div>
            </div>
            
            {/* قائمة مزودي الخدمات */}
            <div className="lg:col-span-3 mt-6 lg:mt-0">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {filteredProviders.length > 0 ? (
                  filteredProviders.map((provider) => (
                    <Link href={`/providers/${provider.id}`} key={provider.id}>
                      <div className="bg-white rounded-lg shadow-sm overflow-hidden hover:shadow-md transition-shadow">
                        <div className="p-4">
                          <div className="flex items-start">
                            <div className="relative h-16 w-16 rounded-full overflow-hidden border-2 border-gray-200">
                              <Image
                                src={provider.avatar}
                                alt={provider.name}
                                width={64}
                                height={64}
                                className="object-cover"
                              />
                              {provider.isOnline && (
                                <span className="absolute bottom-0 left-0 h-3 w-3 rounded-full bg-green-400 border-2 border-white"></span>
                              )}
                            </div>
                            <div className="mr-4 flex-1">
                              <div className="flex justify-between">
                                <h3 className="text-lg font-medium text-gray-900">{provider.name}</h3>
                                <p className="text-lg font-medium text-gray-900">${provider.hourlyRate}/ساعة</p>
                              </div>
                              <div className="flex items-center mt-1">
                                <StarIcon className="h-4 w-4 text-yellow-400 fill-current" />
                                <span className="text-sm text-gray-700 mr-1">
                                  {provider.rating} ({provider.reviewCount} تقييم)
                                </span>
                                <span className="text-sm text-gray-500 mr-2">•</span>
                                <span className="text-sm text-gray-700">{provider.completedJobs} مشروع مكتمل</span>
                              </div>
                            </div>
                          </div>
                          
                          <div className="mt-3">
                            <p className="text-sm text-gray-600 line-clamp-2">{provider.bio}</p>
                          </div>
                          
                          <div className="mt-3">
                            <div className="flex flex-wrap gap-2">
                              {provider.specialties.slice(0, 3).map((specialty) => (
                                <span
                                  key={specialty}
                                  className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800"
                                >
                                  {specialty}
                                </span>
                              ))}
                              {provider.specialties.length > 3 && (
                                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                                  +{provider.specialties.length - 3}
                                </span>
                              )}
                            </div>
                          </div>
                          
                          <div className="mt-3 flex justify-between items-center text-xs text-gray-500">
                            <span>{provider.responseTime} وقت الاستجابة</span>
                            <span className="text-indigo-600 hover:text-indigo-800">عرض الملف الشخصي</span>
                          </div>
                        </div>
                      </div>
                    </Link>
                  ))
                ) : (
                  <div className="col-span-full text-center py-10">
                    <p className="text-gray-500 text-lg">لم يتم العثور على مزودي خدمات مطابقين للبحث</p>
                    <button
                      onClick={() => {
                        setSearchTerm('');
                        setSelectedSpecialties([]);
                        setPriceRange([0, 100]);
                        setOnlineOnly(false);
                      }}
                      className="mt-2 text-indigo-600 hover:text-indigo-800"
                    >
                      إعادة ضبط الفلاتر
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  );
} 